@media only screen and (max-width:1279px) {
    html{
        font-size:58% !important;
    }
    .nav-logo{
        width:15%;
    }
    .main-container .left-navbar{
        padding-left:0;
    }
    .question-list .question-item .btn-col{
        width:200px;
    }


}
@media only screen and (max-width:1144px) {
    html{
        font-size:45% !important;
    }
    .question-list .question-item .btn-col{
        width:150px;
    }


}
@media only screen and (max-width:1000px) {
    html{
        font-size:50%;
    }

    .left-navbar .user-info img{
        width:100px;
        height:100px;
    }
    .main-container .question-container{
        padding:0;
    }
    .nav-container{
        height:90px;
    }
    .popup{
        padding:0 50px;
    }
    .question-list .question-item .btn-col{
        width:180px;
    }
    .left-navbar .user-info span{
        top:70px;
        right:46px;
        width:30px;
        height:30px;
    }
    .left-navbar .user-info svg{
        height:20px;
        height:20px;
    }
    .nav-item-user div:first-child{
        opacity: 0;
        display: none;
    }
    .nav-item-user .hamburger-menu{
        display:block;
        opacity: 1;
        width:10rem ;
        height:10rem;
        margin-top:40px;
    }
    .nav-item-links{
        flex:1;
    }
    .nav-item-user .hamburger-menu span{
        display:block;
        width:3rem;
        height:2px;
       
        background-color:#000;
    }
    .nav-item-user .hamburger-menu span:nth-child(2){
        margin:5px 0;
    }
    .left-navbar .user-info .private-info{
        width:20rem;
    }
    .exam-schedual .search-exam{
        width:100%;
    }
    .exam-schedual .exam-list .row{
        flex-wrap: wrap;

    }
    .exam-list .row .exam-item{
        width:50%;
    }
    .col-1,
    .col-2,
    .col-3{
        padding:0 !important;
        text-align: center;
    }
}
@media only screen and (max-width: 790px) {
    .left-navbar{
        width:40%;
    }
    nav .nav-logo-about{
        display:none !important;
    }
    nav ul .bg-btn{
        right:60px;
    }
    
    .left-navbar .user-info{
        
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .nav-item a{
        font-size:1.5rem;
    }
    .left-navbar .user-info span{
        right:26px;
    }
    .left-navbar .user-info .camera-icon{
        width: 30px;
        height: 30px;
        right: 0;
        bottom: 0;
    }
    .exam-list .row .exam-item{
        width:100%;
    }

}
@media only screen and (max-width: 650px){
    
    .nav-item .nav-item-links{
        display:none;
        opacity:0;
    }
    .left-navbar .user-info span{
        right:42%;
    }
    .popup .btn{
        padding:12.5px 50px;
    }
    .question-list .question-item .btn-col{
        width:100px;
    }
    .nav-item-user .hamburger-menu{
        width:4rem;
    }
    .nav-logo{
        width:90%;
    }
    .main-container{
        flex-direction:column;
    }
    .left-navbar{
        width:100% !important;
    }
    .left-navbar .user-list{
        display:flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .nav-item .nav-item-user{
        padding:0;
    }
    .left-navbar .user-list .btn{
        width:30%;
    }
    .right-content{
        width:100% !important;
    }
    .right-content table{
        font-size:1rem;
    }
    .col-1,
    .col-2,
    .col-3{
        padding:0 !important;
        text-align: center;
    }
}


