@media only screen and (max-width: 768px) {
    nav .nav-logo-about{
        display:none !important;
    }
    .show-box .question-box{
        width:60%;
    }
    .navbar .nav-container{
        height:90px;
        padding-left:10px;
        justify-content: center;
    }
    .main-container .btn.question-btn{
        width:100%;
    }
    .question-list .question-item .btn-col{
        margin-right:50px;
    }
    
    .question-list .question-item:nth-child(3){
        margin:0;
    }
    .question-list .question-item:not(:last-child){
        padding:0;
    }
    .tox:not([dir=rtl]){
        width:100%;
    }
    .popup{
        padding:0;
    }
    
    .main-container .question-item{
        padding:0;
    }
    .question-list .question-item .btn-col{
        margin-right:0;
        margin-left:10px;
    }
    .popup-fail .btn, .popup-fail .btn.primary-bg{
        padding:10px 50px;
    }
}
@media only screen and (max-width: 500px){
    .show-box .question-box{
        width:100%;
    }
    nav ul div.flex{
        padding-right:0;
        
    }
    nav ul .bg-btn{
        right:24.77px;
        width:40%;
    }
    .popup .btn{
        font-size:1.3rem;
    }
    .question-list .question-item .btn-col{
        width:100px;
    }
    .popup{
        min-height:300px;
        width:100%;
    }
    .popup-fail,
    .popup-success{
        width:100%;
    }
    .popup h1{
        font-size:1.5rem;
        line-height:normal;
    }
    #final-submit{
        padding:10px 42px;
    }
    
    .popup .btn, .popup .btn.primary-bg{
        padding:10px 40px;
    }
    .btn.primary-bg{
        padding:10px 30px;
    }
    .btn.primary-bg a{
        font-size:1.3rem;
    }
    .popup-success .btn.delete-popup-btn{
        padding:10px 18px;
    }
    .popup-success .btn.primary-bg{
        padding:10px 10px;
    }
}
@media (max-width: 440px){
    ul li.inline{
        margin: 0 10px !important;
    }
    .vertical-line{
        display:none;
    }
    .btn .bg-btn{
        position: absolute;
    }
    .col-right:after,
    .col-left:after{
        display:none;
    }
    .question-list .question-item .btn-col{
        width:70px;
    }
    nav .title,
    nav ul li .hour-icon{
        display:none;
    }
}
:root{
    --primary-color:#049435;
    --grey-color:#E1E5F1;
}
*{
    margin:0;
    padding:0;
    box-sizing: inherit;
    font-family: 'Mulish', sans-serif;
}
html{
    box-sizing: border-box;
    font-size:62.5%;
}
body{
    font-family: 'Mulish', sans-serif;
}
img{
    max-width: 100%;
    max-height:100%;
}
ul li{
    list-style: none;
}
a{
    text-decoration: none;
    color:#fff;
}
.flex{
    display:flex;
}
.fl-1{
    flex:1 1;
}
.flex-row{
    flex-direction: row;
}
.jc-center{
    justify-content: center;
}
.fl-wrap{
    flex-wrap: wrap;
}
.jc-sb{
    justify-content: space-between;
}
.ai-center{
    align-items: center;
}
.btn{
    outline:none;
    border:none;
    background-color:#E1E5F1;
    color:#000;
    cursor: pointer;
}
.primary-bg{
    background-color:#00BF2A !important;
    color:#fff !important;
    border:none !important;
}
.active{
    background-color:#049435;
    background-color:var(--primary-color);
    color:#fff !important;
}

.active-question{
    background-color:#FF9E0D;
    border:none;
    color:#fff;
}
.left-navbar{
    width:20.2777%;
}
.right-content{
    width:80%;
}
.line{
    width:100%;
    height:1px;
    margin:16px 0;
    background-color:#E1E5F1;
    background-color:var(--grey-color);
    clear: both;
}
.primary-line{
    background-color:#049435;
    background-color:var(--primary-color);
    height:5px;
    border-radius:5px;
}
.vertical-line{
    height:18px;
    width:1px;
    background-color:#E1E5F1;
    background-color:var(--grey-color);
}
input:focus{
   
    outline: none;
}
.dark-text{
    color:#091230;
    font-weight:600;
    
}
table, th, td {
    border: 1px solid #CCD1DC;
    border-collapse: collapse;
  }
table{
    width:100%;
    font-size:1.8rem;
    line-height:25px;
    max-width:1018px;
    color:#363E57;
    font-weight:600;
    
}
th, td{
    height:57px;
}
th{
    background-color:#E1E5F1;
    background-color:var(--grey-color);
    text-align: left;
}
.col-1{
    color:#091230;
    padding-left:30px;
    width:16.69%;
    
    
}
.col-2{
    text-align:left;   
    padding-left:30px;
    width:36.149%;
}
.col-3{
    padding-left:30px;
    text-align: left;
    width:12.67%;
}
.col-4{
   
    width:23.968%;
    text-align: center;
}
.col-5{
    
    width:10.5%;
    text-align: center;
}
.check-icon{
    color:#2DD348;
}
.fa-times{
    color:#F0424C;
}
.hamburger-menu{
    display:none;
}
.toggle{
    position: relative;
    z-index: 5;;

}
.toggle span:first-child{
    transform:rotate(45deg);
    transform-origin: 1px;

}
.toggle span:nth-child(2){
    opacity: 0;;
}
.toggle span:last-child{
    transform:rotate(-45deg);
    transform-origin: 3px;
}
.nav-item-user div:first-child.overlay-box{
    position:fixed;
    opacity: 1;
    display:block;
    left:0;
    top:0;
    bottom:0;
    right:0;
    background-color:rgba(0,0,0,0.7);
    z-index:4;
    
}
.nav-item-user div:first-child.overlay-box ul{
    flex-direction:column;
    margin-top:30px;
}
.nav-item-user div:first-child.overlay-box ul li{
    margin:10px 0;
}

.nav-item .nav-item-links.overlay-box{
    position:fixed;
    opacity: 1;
    display:block;
    left:0;
    top:0;
    bottom:0;
    right:0;
    background-color:rgba(0,0,0,0.7);
    z-index:4;
}
